*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 14px;
  line-height: 1.5;
  text-align: center;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  min-height: 100vh;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  background: #f8f8f8;
  font-family: "Balsamiq Sans", cursive;
  color: #3a3a3a;
  gap: 5%;
}
body h1 {
  font-size: 3em;
  font-style: italic;
  color: #009063;
}
body a {
  text-decoration: none;
  transition: all 0.5s ease;
}
body a:visited {
  color: #009063;
}
body a:hover {
  color: #009063;
  border-bottom: 1px solid #009063;
}

@media (min-width: 1920px) {
  html {
    font-size: 16px;
  }
}
@media (min-width: 2560px) {
  html {
    font-size: 24px;
  }
}
.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  position: relative;
  z-index: 1;
}
.hero ::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: url(../../images/hero-background.jpg);
  background-size: cover;
  background-position: center;
  opacity: 0.35;
}
.hero h1 {
  font-size: 7vw;
  line-height: 2;
}
.hero h1 span {
  display: block;
  font-size: 20vw;
}

@media (min-width: 768px) {
  .hero h1 {
    font-size: 3vw;
  }
  .hero h1 span {
    font-size: 13vw;
  }
}
.portfolio {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  height: 100%;
  width: 100%;
}

.portfolio-animated {
  display: grid;
  grid-template-rows: 10% 40% 50%;
  height: 100%;
  width: 80vw;
}
.portfolio-animated .portfolio-image {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
.portfolio-animated .portfolio-image img {
  height: 70%;
  width: auto;
  border: 5px inset #3a3a3a;
  box-shadow: -10px 10px 15px 0px #3a3a3a;
}
.portfolio-animated .portfolio-content {
  display: flex;
  height: 75%;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: justify;
}
.portfolio-animated .portfolio-content .portfolio-about_me {
  display: flex;
  height: 80%;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  text-align: justify;
  padding: 2.5%;
}

.experience {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 150vh;
  width: 100%;
}
.experience .experience-animated {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 2.5%;
  height: 100%;
  width: 80%;
  box-shadow: -10px 10px 10px 5px #3a3a3a;
  margin-bottom: 5%;
}
.experience .experience-animated .experience-card {
  display: grid;
  place-items: center;
  grid-template-rows: 15% 70% 15%;
  height: 100%;
  width: 100%;
}
.experience .experience-animated .experience-card .experience-title {
  height: 100%;
}
.experience .experience-animated .experience-card .experience-title h2 {
  margin: 0.5em 0;
  border-bottom: 2px solid #3a3a3a;
}
.experience .experience-animated .experience-card .experience-about {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 100%;
  color: #3a3a3a;
}
.experience .experience-animated .experience-card .experience-time {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  height: 100%;
}
.experience .experience-animated .experience-card .experience-time p {
  height: fit-content;
  color: #009063;
  background-color: #f8f8f8;
  border: 2px solid #009063;
  box-shadow: -10px 10px 15px 0px #3a3a3a;
  padding: 10%;
  border-radius: 50%;
}

@media (min-width: 768px) {
  .experience .experience-animated .experience-card {
    height: 90%;
  }
}
@media (min-width: 1024px) {
  .experience {
    height: 200vh;
  }
  .experience .experience-animated .experience-card {
    height: 95%;
  }
}
@media (min-width: 1440px) {
  .experience {
    height: 250vh;
  }
  .experience .experience-animated .experience-card .experience-time {
    align-items: center;
  }
}
.skills {
  display: grid;
  place-items: center;
  grid-template-rows: 20% 80%;
  height: 100vh;
  width: 100vw;
}
.skills .skills-title {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
.skills .skills-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 90vw;
  margin: 0 auto;
  gap: 1rem;
}
.skills .skills-content > * {
  width: 135px;
}
.skills .skills-content > * h3 {
  color: #009063;
  background-color: #f8f8f8;
  border: 2px solid #009063;
  box-shadow: -10px 10px 15px 0px #3a3a3a;
  border-radius: 5%;
  padding: 3%;
}

@media (min-width: 768px) {
  .skills .skills-content {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    max-width: 80vw;
    margin: 0 auto;
    gap: 1rem;
    flex-wrap: unset;
  }
  .skills .skills-content .skills-card {
    width: unset;
  }
  .skills .skills-content .skills-card h3 {
    color: #009063;
    background-color: #f8f8f8;
    border: 2px solid #009063;
    box-shadow: -10px 10px 15px 0px #3a3a3a;
    border-radius: 5%;
    padding: 3%;
  }
}
@media (min-width: 1024px) {
  .skills .skills-content {
    gap: 2rem;
  }
}
.projects {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 400vh;
  width: 100vw;
}
.projects .projects-container {
  display: grid;
  grid-template-rows: 10% 90%;
  height: 100%;
  width: 80%;
}
.projects .projects-container .projects-title {
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
}
.projects .projects-container .projects-content_mobile {
  display: none;
}
.projects .projects-container .projects-content {
  display: grid;
  grid-template-rows: repeat(3, 33%);
  gap: 5%;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.projects .projects-container .projects-content .projects-first_project {
  display: flex;
  height: 100%;
  width: 80vw;
  justify-content: center;
  align-items: center;
}
.projects .projects-container .projects-content .projects-second_project {
  display: flex;
  height: 100%;
  width: 80vw;
  justify-content: center;
  align-items: center;
}
.projects .projects-container .projects-content .projects-third_project {
  display: flex;
  height: 100%;
  width: 80vw;
  justify-content: center;
  align-items: center;
}

@media (max-width: 1024px) {
  .projects {
    height: 85vh;
  }
  .projects .projects-container .projects-content {
    display: none;
  }
  .projects .projects-container .projects-content_mobile {
    display: grid;
    grid-template-rows: repeat(3, 33%);
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
  }
  .projects .projects-container .projects-content_mobile > * {
    display: flex;
    flex-direction: column;
    gap: 5%;
    width: 100%;
    height: 60%;
    align-items: center;
    justify-content: space-between;
  }
  .projects .projects-container .projects-content_mobile > * a {
    border-bottom: 1px solid #3a3a3a;
  }
}
@media (min-width: 1440px) {
  .projects {
    height: 300vh;
  }
  .projects .projects-container .projects-content {
    display: grid;
    grid-template-rows: repeat(3, 33%);
    gap: 5%;
    height: 80%;
    width: 100%;
  }
  .projects .projects-container .projects-content_mobile {
    display: none;
  }
}
.contact {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
  width: 100%;
}
.contact .contact-container {
  display: grid;
  grid-template-rows: 20% 60% 20%;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 80vw;
}
.contact .contact-container .contact-title {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.contact .contact-container .contact-content {
  text-align: justify;
}
.contact .contact-container .contact-icons {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 5%;
}
.contact .contact-container .contact-icons span a {
  width: 85%;
  border: none;
}
.contact .contact-container .contact-icons span a img {
  height: auto;
  width: inherit;
}

@media (min-width: 768px) {
  .contact .contact-container {
    grid-template-rows: 20% 40% 40%;
  }
  .contact .contact-container .contact-content {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: justify;
  }
  .contact .contact-container .contact-icons span a {
    width: 45%;
  }
}