@use "../abstracts/" as *;

.skills {
  display: grid;
  place-items: center;
  grid-template-rows: 20% 80%;
  height: 100vh;
  width: 100vw;

  .skills-title {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
  }

  .skills-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    max-width: 90vw;
    margin: 0 auto;
    gap: 1rem;
  }

  .skills-content > * {
    width: 135px;

    h3 {
      color: $clr-highlight;
      background-color: $clr-bg;
      border: 2px solid $clr-highlight;
      box-shadow: -10px 10px 15px 0px $clr-text;
      border-radius: 5%;
      padding: 3%;
    }
  }
}

// MEDIA QUERIES

@media (min-width: $breakpoint-m) {
  .skills {
    .skills-content {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
      max-width: 80vw;
      margin: 0 auto;
      gap: 1rem;
      flex-wrap: unset;

      .skills-card {
        width: unset;

        h3 {
          color: $clr-highlight;
          background-color: $clr-bg;
          border: 2px solid $clr-highlight;
          box-shadow: -10px 10px 15px 0px $clr-text;
          border-radius: 5%;
          padding: 3%;
        }
      }
    }
  }
}

@media (min-width: $breakpoint-l) {
  .skills {
    .skills-content {
      gap: 2rem;
    }
  }
}
